import React, { useState, useMemo } from 'react'
import { FaPlus, FaEdit, FaTrash } from 'react-icons/fa'
import { Project, ProjectFormData } from '../../types'
import ProjectForm from '../forms/ProjectForm'
import { useProjectManagerData } from '../../hooks/useProjectManagerData'

interface ProjectManagerProps {
  projectFilter?: 'all' | 'category' | 'featured'
  projectSearchTerm?: string
  selectedProjectCategory?: string
}

const ProjectManager: React.FC<ProjectManagerProps> = ({ 
  projectFilter = 'all', 
  projectSearchTerm = '', 
  selectedProjectCategory = ''
}) => {
  const { projects, isLoading, createProject, updateProject, deleteProject } = useProjectManagerData()
  const [showForm, setShowForm] = useState(false)
  const [editingItem, setEditingItem] = useState<Project | null>(null)
  const [isSaving, setIsSaving] = useState(false)
  const [isDeleting, setIsDeleting] = useState(false)

  // ÌïÑÌÑ∞ÎßÅÎêú ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù Í≥ÑÏÇ∞
  const filteredProjects = useMemo(() => {
    let filtered = [...projects]

    // ÌïÑÌÑ∞ Ï†ÅÏö©
    if (projectFilter === 'category' && selectedProjectCategory) {
      filtered = filtered.filter(project => project.category === selectedProjectCategory)
    } else if (projectFilter === 'featured') {
      // Ï£ºÏöî ÌîÑÎ°úÏ†ùÌä∏: ÏôÑÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ Ï§ë ÏÉÅÏÑ∏ ÏÑ§Î™ÖÏù¥ ÏûàÎäî Í≤É
      filtered = filtered.filter(project => 
        project.status === 'completed' && 
        project.detailedDescription && 
        project.detailedDescription.trim().length > 0
      )
    }

    // Í≤ÄÏÉâÏñ¥ Ï†ÅÏö©
    if (projectSearchTerm) {
      const searchLower = projectSearchTerm.toLowerCase()
      filtered = filtered.filter(project => 
        project.title.toLowerCase().includes(searchLower) ||
        project.description.toLowerCase().includes(searchLower) ||
        (project.technologies && project.technologies.some(tech => 
          tech.toLowerCase().includes(searchLower)
        )) ||
        (project.category && project.category.toLowerCase().includes(searchLower))
      )
    }

    return filtered
  }, [projects, projectFilter, projectSearchTerm, selectedProjectCategory])

  // Ïπ¥ÌÖåÍ≥†Î¶¨ Î™©Î°ùÏùÄ ÌïÑÏöîÏãú ÏÇ¨Ïö©
  // const projectCategories = useMemo(() => {
  //   const categories = [...new Set(projects.map(project => project.category).filter(Boolean))]
  //   return categories.sort()
  // }, [projects])

  // Ï£ºÏöî ÌîÑÎ°úÏ†ùÌä∏ Ïàò Í≥ÑÏÇ∞
  const featuredProjectsCount = useMemo(() => {
    return projects.filter(project => 
      project.status === 'completed' && 
      project.detailedDescription && 
      project.detailedDescription.trim().length > 0
    ).length
  }, [projects])

  const handleSave = async (data: ProjectFormData) => {
    console.log('üéØ ProjectManager handleSave Ìò∏Ï∂úÎê®')
    console.log('üì¶ Î∞õÏùÄ Îç∞Ïù¥ÌÑ∞:', data)
    console.log('üîó skillIds:', data.skillIds)
    
    try {
      setIsSaving(true)
      console.log('‚è≥ isSaving = true ÏÑ§Ï†ïÎê®')
      
      if (editingItem) {
        console.log('‚úèÔ∏è ÌîÑÎ°úÏ†ùÌä∏ ÏàòÏ†ï Î™®Îìú:', editingItem._id)
        const result = await updateProject(editingItem._id!, data)
        console.log('‚úÖ ÏàòÏ†ï Í≤∞Í≥º:', result)
        if (result.success) {
          setShowForm(false)
          setEditingItem(null)
          alert('‚úÖ ÌîÑÎ°úÏ†ùÌä∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§!')
        } else {
          // üåü ÏÑúÎ≤Ñ Ïò§Î•ò Î©îÏãúÏßÄ ÏÉÅÏÑ∏ Ï∂úÎ†•
          console.error('‚ùå ÏÑúÎ≤Ñ Í±∞Î∂Ä Ïù¥Ïú†:', result.error)
          const errorMsg = (result.error as any)?.response?.data?.message || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'
          alert(`‚ùå ÌîÑÎ°úÏ†ùÌä∏ ÏàòÏ†ï Ïã§Ìå®: ${errorMsg}`)
          if ((result.error as any)?.response?.data) {
            console.error('üìã ÏÑúÎ≤Ñ ÏùëÎãµ ÏÉÅÏÑ∏:', (result.error as any).response.data)
          }
        }
      } else {
        console.log('‚ûï ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ± Î™®Îìú')
        const result = await createProject(data)
        console.log('‚úÖ ÏÉùÏÑ± Í≤∞Í≥º:', result)
        if (result.success) {
          setShowForm(false)
          alert('‚úÖ ÌîÑÎ°úÏ†ùÌä∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§!')
        } else {
          // üåü ÏÑúÎ≤Ñ Ïò§Î•ò Î©îÏãúÏßÄ ÏÉÅÏÑ∏ Ï∂úÎ†•
          console.error('‚ùå ÏÑúÎ≤Ñ Í±∞Î∂Ä Ïù¥Ïú†:', result.error)
          const errorMsg = (result.error as any)?.response?.data?.message || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'
          alert(`‚ùå ÌîÑÎ°úÏ†ùÌä∏ Ï∂îÍ∞Ä Ïã§Ìå®: ${errorMsg}`)
          if ((result.error as any)?.response?.data) {
            console.error('üìã ÏÑúÎ≤Ñ ÏùëÎãµ ÏÉÅÏÑ∏:', (result.error as any).response.data)
          }
        }
      }
    } catch (error: any) {
      console.error('‚ùå Ï†ÄÏû• Ïã§Ìå®:', error)
      // üåü Axios Ïò§Î•ò ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Ï∂úÎ†•
      const errorMsg = error?.response?.data?.message || error.message || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'
      alert(`‚ùå Ï†ÄÏû• Ï§ë Ïò§Î•ò Î∞úÏÉù: ${errorMsg}`)
      if (error?.response?.data) {
        console.error('üìã ÏÑúÎ≤Ñ ÏùëÎãµ ÏÉÅÏÑ∏:', error.response.data)
        console.error('üî¢ ÏÉÅÌÉú ÏΩîÎìú:', error.response.status)
      }
    } finally {
      console.log('üèÅ isSaving = false ÏÑ§Ï†ïÎê®')
      setIsSaving(false)
    }
  }

  const handleCancel = () => {
    setShowForm(false)
    setEditingItem(null)
  }

  const handleDelete = async (project: Project) => {
    if (confirm('Ï†ïÎßê ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) {
      try {
        setIsDeleting(true)
        await deleteProject(project._id!)
      } catch (error) {
        console.error('ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      } finally {
        setIsDeleting(false)
      }
    }
  }

  const handleAddNew = () => {
    setEditingItem(null)
    setShowForm(true)
  }

  const handleEdit = (project: Project) => {
    setEditingItem(project)
    setShowForm(true)
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center py-8">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        <span className="ml-2">ÌîÑÎ°úÏ†ùÌä∏ Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</span>
      </div>
    )
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold">ÌîÑÎ°úÏ†ùÌä∏ Í¥ÄÎ¶¨</h2>
        <button
          onClick={handleAddNew}
          className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex items-center gap-2"
        >
          <FaPlus />
          ÌîÑÎ°úÏ†ùÌä∏ Ï∂îÍ∞Ä
        </button>
      </div>

      {/* ÌïÑÌÑ∞ Ï†ïÎ≥¥ ÌëúÏãú */}
      {(projectFilter !== 'all' || projectSearchTerm || selectedProjectCategory) && (
        <div className="mb-4 p-4 bg-gray-100 dark:bg-gray-700 rounded-lg">
          <div className="flex items-center justify-between">
            <div>
              <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300">
                ÌòÑÏû¨ ÌïÑÌÑ∞: 
                {projectFilter === 'all' && !projectSearchTerm && ' üìÅ Î™®Îì† ÌîÑÎ°úÏ†ùÌä∏'}
                {projectFilter === 'category' && selectedProjectCategory && ` üè∑Ô∏è ${selectedProjectCategory} Ïπ¥ÌÖåÍ≥†Î¶¨`}
                {projectFilter === 'featured' && ' üåü Ï£ºÏöî ÌîÑÎ°úÏ†ùÌä∏'}
                {projectSearchTerm && ` üîç "${projectSearchTerm}" Í≤ÄÏÉâ`}
              </h4>
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                {filteredProjects.length}Í∞ú ÌîÑÎ°úÏ†ùÌä∏ ÌëúÏãú Ï§ë (Ï†ÑÏ≤¥ {projects.length}Í∞ú)
                {projectFilter === 'featured' && ` (Ï£ºÏöî ÌîÑÎ°úÏ†ùÌä∏: ${featuredProjectsCount}Í∞ú)`}
              </p>
            </div>
          </div>
        </div>
      )}
      
      {showForm && (
        <div className="mb-6">
          <ProjectForm
            data={editingItem}
            onSave={handleSave}
            onCancel={handleCancel}
            isSaving={isSaving}
          />
        </div>
      )}

      {/* ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù */}
      {filteredProjects.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredProjects.map((project) => (
            <div key={project._id} className="bg-gray-50 dark:bg-gray-700 p-4 rounded-lg border border-gray-200 dark:border-gray-600">
              <h3 className="font-semibold text-lg mb-2">{project.title}</h3>
              <p className="text-gray-600 dark:text-gray-400 text-sm mb-2 line-clamp-2">{project.description}</p>
              {project.category && (
                <p className="text-xs text-blue-600 dark:text-blue-400 mb-3">{project.category}</p>
              )}
              {project.technologies && project.technologies.length > 0 && (
                <div className="flex flex-wrap gap-1 mb-3">
                  {project.technologies.slice(0, 3).map((tech, index) => (
                    <span key={index} className="text-xs bg-gray-200 dark:bg-gray-600 text-gray-700 dark:text-gray-300 px-2 py-1 rounded">
                      {tech}
                    </span>
                  ))}
                  {project.technologies.length > 3 && (
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      +{project.technologies.length - 3}
                    </span>
                  )}
                </div>
              )}
              <div className="flex justify-between items-center">
                <div className="flex items-center gap-2">
                  <span className={`px-2 py-1 rounded text-xs ${
                    project.status === 'completed' 
                      ? 'bg-green-100 dark:bg-green-900/30 text-green-800 dark:text-green-300' 
                      : 'bg-yellow-100 dark:bg-yellow-900/30 text-yellow-800 dark:text-yellow-300'
                  }`}>
                    {project.status === 'completed' ? 'ÏôÑÎ£å' : 'ÏßÑÌñâÏ§ë'}
                  </span>
                  {project.status === 'completed' && project.detailedDescription && project.detailedDescription.trim().length > 0 && (
                    <span className="text-xs bg-purple-100 dark:bg-purple-900/30 text-purple-800 dark:text-purple-300 px-2 py-1 rounded">
                      üåü Ï£ºÏöî
                    </span>
                  )}
                </div>
                <div className="flex gap-2">
                  <button
                    onClick={() => handleEdit(project)}
                    className="p-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
                    disabled={isSaving || isDeleting}
                  >
                    <FaEdit />
                  </button>
                  <button
                    onClick={() => handleDelete(project)}
                    className="p-2 bg-red-600 text-white rounded hover:bg-red-700 transition-colors"
                    disabled={isSaving || isDeleting}
                  >
                    <FaTrash />
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="text-center py-12">
          <p className="text-gray-500 dark:text-gray-400 text-lg mb-4">
            {projectFilter !== 'all' || projectSearchTerm || selectedProjectCategory
              ? 'ÌïÑÌÑ∞ Ï°∞Í±¥Ïóê ÎßûÎäî ÌîÑÎ°úÏ†ùÌä∏Í∞Ä ÏóÜÏäµÎãàÎã§.'
              : 'Îì±Î°ùÎêú ÌîÑÎ°úÏ†ùÌä∏Í∞Ä ÏóÜÏäµÎãàÎã§.'
            }
          </p>
          {(projectFilter !== 'all' || projectSearchTerm || selectedProjectCategory) && (
            <p className="text-sm text-gray-400 dark:text-gray-500">
              ÌïÑÌÑ∞Î•º Ï¥àÍ∏∞ÌôîÌïòÍ±∞ÎÇò Îã§Î•∏ Í≤ÄÏÉâÏñ¥Î•º ÏãúÎèÑÌï¥Î≥¥ÏÑ∏Ïöî.
            </p>
          )}
        </div>
      )}
    </div>
  )
}

export default ProjectManager