import { useState, useCallback } from 'react'

interface CRUDState<T> {
  showForm: boolean
  editingItem: T | null
  isSaving: boolean
  isDeleting: boolean
}

interface CRUDActions<T> {
  handleCreate: () => void
  handleEdit: (item: T) => void
  handleCancel: () => void
  handleSave: (data: any) => Promise<void>
  handleDelete: (item: T) => Promise<void>
  setShowForm: (show: boolean) => void
  setEditingItem: (item: T | null) => void
}

interface UseCRUDManagerOptions<T> {
  onSave: (data: any, editingItem?: T | null) => Promise<void>
  onDelete: (item: T) => Promise<void>
  onSuccess?: () => void
  onError?: (error: Error) => void
}

/**
 * CRUD Í¥ÄÎ¶¨ÏûêÎ•º ÏúÑÌïú Ïû¨ÏÇ¨Ïö© Í∞ÄÎä•Ìïú Custom Hook
 * BookManager, ExperienceManager, ProjectManager Îì±ÏóêÏÑú Í≥µÌÜµÏúºÎ°ú ÏÇ¨Ïö©ÎêòÎäî
 * ÏÉÅÌÉú Í¥ÄÎ¶¨ Î°úÏßÅÏùÑ Ï§ëÏïôÌôîÌïòÏó¨ ÏΩîÎìú Ï§ëÎ≥µÏùÑ Ï†úÍ±∞Ìï©ÎãàÎã§.
 */
export function useCRUDManager<T>({
  onSave,
  onDelete,
  onSuccess,
  onError
}: UseCRUDManagerOptions<T>): [CRUDState<T>, CRUDActions<T>] {
  
  const [showForm, setShowForm] = useState(false)
  const [editingItem, setEditingItem] = useState<T | null>(null)
  const [isSaving, setIsSaving] = useState(false)
  const [isDeleting, setIsDeleting] = useState(false)

  // ÏÉà Ìï≠Î™© ÏÉùÏÑ± Ìï∏Îì§Îü¨
  const handleCreate = useCallback(() => {
    setEditingItem(null)
    setShowForm(true)
  }, [])

  // Í∏∞Ï°¥ Ìï≠Î™© Ìé∏Ïßë Ìï∏Îì§Îü¨
  const handleEdit = useCallback((item: T) => {
    setEditingItem(item)
    setShowForm(true)
  }, [])

  // Ìèº Ï∑®ÏÜå Ìï∏Îì§Îü¨
  const handleCancel = useCallback(() => {
    setShowForm(false)
    setEditingItem(null)
    setIsSaving(false)
    setIsDeleting(false)
  }, [])

  // Ï†ÄÏû• Ìï∏Îì§Îü¨ (ÎÇôÍ¥ÄÏ†Å ÏóÖÎç∞Ïù¥Ìä∏ ÏßÄÏõê)
  const handleSave = useCallback(async (data: any) => {
    if (isSaving) return

    try {
      setIsSaving(true)
      await onSave(data, editingItem)
      
      // ÏÑ±Í≥µ Ïãú Ìèº Îã´Í∏∞
      handleCancel()
      onSuccess?.()
    } catch (error) {
      console.error('Ï†ÄÏû• Ïã§Ìå®:', error)
      onError?.(error as Error)
    } finally {
      setIsSaving(false)
    }
  }, [isSaving, editingItem, onSave, onSuccess, onError, handleCancel])

  // ÏÇ≠Ï†ú Ìï∏Îì§Îü¨
  const handleDelete = useCallback(async (item: T) => {
    if (isDeleting) return
    
    // ÏÇ¨Ïö©Ïûê ÌôïÏù∏
    const confirmed = window.confirm(
      `Ï†ïÎßê "${(item as any).title || (item as any).name || 'Ïù¥ Ìï≠Î™©'}"ÏùÑ(Î•º) ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?`
    )
    
    if (!confirmed) return

    try {
      setIsDeleting(true)
      await onDelete(item)
      onSuccess?.()
    } catch (error) {
      console.error('ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      onError?.(error as Error)
    } finally {
      setIsDeleting(false)
    }
  }, [isDeleting, onDelete, onSuccess, onError])

  const state: CRUDState<T> = {
    showForm,
    editingItem,
    isSaving,
    isDeleting
  }

  const actions: CRUDActions<T> = {
    handleCreate,
    handleEdit,
    handleCancel,
    handleSave,
    handleDelete,
    setShowForm,
    setEditingItem
  }

  return [state, actions]
}

/**
 * ÎÇôÍ¥ÄÏ†Å ÏóÖÎç∞Ïù¥Ìä∏Î•º ÏßÄÏõêÌïòÎäî CRUD Í¥ÄÎ¶¨Ïûê Hook
 * API Ìò∏Ï∂ú Ï†ÑÏóê UIÎ•º Î®ºÏ†Ä ÏóÖÎç∞Ïù¥Ìä∏ÌïòÏó¨ Îçî ÎÇòÏùÄ ÏÇ¨Ïö©Ïûê Í≤ΩÌóòÏùÑ Ï†úÍ≥µÌï©ÎãàÎã§.
 */
export function useOptimisticCRUDManager<T>({
  onSave,
  onDelete,
  onOptimisticUpdate,
  onOptimisticDelete,
  onSuccess,
  onError
}: UseCRUDManagerOptions<T> & {
  onOptimisticUpdate?: (data: any, editingItem?: T | null) => void
  onOptimisticDelete?: (item: T) => void
}) {
  
  const [state, actions] = useCRUDManager<T>({
    onSave: async (data, editingItem) => {
      // ÎÇôÍ¥ÄÏ†Å ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìñâ
      onOptimisticUpdate?.(data, editingItem)
      
      try {
        await onSave(data, editingItem)
      } catch (error) {
        // Ïã§Ìå® Ïãú Î°§Î∞± (ÏÉÅÏúÑ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï≤òÎ¶¨)
        throw error
      }
    },
    onDelete: async (item) => {
      // ÎÇôÍ¥ÄÏ†Å ÏÇ≠Ï†ú Ïã§Ìñâ
      onOptimisticDelete?.(item)
      
      try {
        await onDelete(item)
      } catch (error) {
        // Ïã§Ìå® Ïãú Î°§Î∞± (ÏÉÅÏúÑ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï≤òÎ¶¨)
        throw error
      }
    },
    onSuccess,
    onError
  })

  return [state, actions] as const
}

export default useCRUDManager
