import { useState, useEffect, useCallback } from 'react'
import { useAbortController } from './useAbortController'
import { useOptimisticArrayUpdate } from './useOptimisticUpdate'
import { useToastHelpers } from './useToast'
import { booksAPI } from '../services/api'
import { Book, BookFormData, BookUpdateData } from '../types'

/**
 * Î¶¨Ìå©ÌÜ†ÎßÅÎêú BookManager Îç∞Ïù¥ÌÑ∞ Í¥ÄÎ¶¨ Hook
 * 
 * Ï£ºÏöî Í∞úÏÑ†ÏÇ¨Ìï≠:
 * 1. AbortControllerÎ•º ÏÇ¨Ïö©Ìïú Î©îÎ™®Î¶¨ ÎàÑÏàò Î∞©ÏßÄ
 * 2. ÎÇôÍ¥ÄÏ†Å ÏóÖÎç∞Ïù¥Ìä∏Î°ú ÏÑ±Îä• Í∞úÏÑ†
 * 3. Toast ÏïåÎ¶ºÏúºÎ°ú ÏÇ¨Ïö©Ïûê ÌîºÎìúÎ∞± Í∞úÏÑ†
 * 4. ÏóêÎü¨ Ï≤òÎ¶¨ Í∞ïÌôî
 */
export function useBookManagerData() {
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const { createAbortController } = useAbortController()
  const { success, error: showError } = useToastHelpers()

  // ÎÇôÍ¥ÄÏ†Å ÏóÖÎç∞Ïù¥Ìä∏Î•º ÏÇ¨Ïö©Ìïú ÏÑúÏ†Å Îç∞Ïù¥ÌÑ∞ Í¥ÄÎ¶¨
  const {
    data: books,
    isOptimistic,
    optimisticAdd,
    optimisticUpdateItem,
    optimisticDelete,
    resetData
  } = useOptimisticArrayUpdate<Book>([])

  // ÏÑúÏ†Å Îç∞Ïù¥ÌÑ∞ Î°úÎìú
  const fetchBooks = useCallback(async () => {
    try {
      setIsLoading(true)
      setError(null)
      
      const abortController = createAbortController()
      const response = await booksAPI.getAll()
      
        if (!abortController.signal.aborted) {
          resetData(response.data.data || [])
        }
    } catch (err) {
      if (!(err as Error).name?.includes('AbortError')) {
        const errorMessage = 'ÏÑúÏ†Å Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.'
        setError(errorMessage)
        showError('Î°úÎìú Ïã§Ìå®', errorMessage)
      }
    } finally {
      setIsLoading(false)
    }
  }, [createAbortController, resetData, showError])

  // ÏÑúÏ†Å ÏÉùÏÑ±
  const createBook = useCallback(async (bookData: BookFormData) => {
    try {
      const newBook = {
        ...bookData,
        _id: `temp_${Date.now()}`,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString()
      } as Book

      const result = await optimisticAdd(
        newBook,
        () => booksAPI.create(bookData).then(res => res.data)
      )

      success('ÏÑúÏ†Å Ï∂îÍ∞Ä ÏôÑÎ£å', `${bookData.title}Ïù¥(Í∞Ä) ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§.`)
      return result
    } catch (err) {
      showError('ÏÑúÏ†Å Ï∂îÍ∞Ä Ïã§Ìå®', err instanceof Error ? err.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.')
      throw err
    }
  }, [optimisticAdd, success, showError])

  // ÏÑúÏ†Å ÏàòÏ†ï
  const updateBook = useCallback(async (bookId: string, bookData: BookUpdateData) => {
    try {
      const result = await optimisticUpdateItem(
        bookId,
        bookData,
        () => booksAPI.update(bookId, bookData).then(res => res.data)
      )

      success('ÏÑúÏ†Å ÏàòÏ†ï ÏôÑÎ£å', 'ÏÑúÏ†Å Ï†ïÎ≥¥Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.')
      return result
    } catch (err) {
      showError('ÏÑúÏ†Å ÏàòÏ†ï Ïã§Ìå®', err instanceof Error ? err.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.')
      throw err
    }
  }, [optimisticUpdateItem, success, showError])

  // ÏÑúÏ†Å ÏÇ≠Ï†ú
  const deleteBook = useCallback(async (bookId: string) => {
    try {
      const result = await optimisticDelete(
        bookId,
        () => booksAPI.delete(bookId).then(() => {})
      )

      success('ÏÑúÏ†Å ÏÇ≠Ï†ú ÏôÑÎ£å', 'ÏÑúÏ†ÅÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.')
      return result
    } catch (err) {
      showError('ÏÑúÏ†Å ÏÇ≠Ï†ú Ïã§Ìå®', err instanceof Error ? err.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.')
      throw err
    }
  }, [optimisticDelete, success, showError])

  // Ï¥àÍ∏∞ Îç∞Ïù¥ÌÑ∞ Î°úÎìú
  useEffect(() => {
    fetchBooks()
  }, [])

  return {
    books,
    isLoading,
    error,
    isOptimistic,
    createBook,
    updateBook,
    deleteBook,
    refetch: fetchBooks
  }
}

export default useBookManagerData