import { useEffect, useRef, useCallback } from 'react'

/**
 * AbortControllerÎ•º ÏÇ¨Ïö©ÌïòÏó¨ API ÏöîÏ≤≠ Ï∑®ÏÜåÎ•º Í¥ÄÎ¶¨ÌïòÎäî Hook
 * Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú ÎòêÎäî ÏùòÏ°¥ÏÑ± Î≥ÄÍ≤Ω Ïãú Ïù¥Ï†Ñ ÏöîÏ≤≠ÏùÑ Ï∑®ÏÜåÌïòÏó¨
 * Î©îÎ™®Î¶¨ ÎàÑÏàòÏôÄ Race ConditionÏùÑ Î∞©ÏßÄÌï©ÎãàÎã§.
 */
export function useAbortController() {
  const abortControllerRef = useRef<AbortController | null>(null)

  // ÏÉàÎ°úÏö¥ AbortController ÏÉùÏÑ±
  const createAbortController = useCallback(() => {
    // Ïù¥Ï†Ñ Ïª®Ìä∏Î°§Îü¨Í∞Ä ÏûàÎã§Î©¥ Ï∑®ÏÜå
    if (abortControllerRef.current) {
      abortControllerRef.current.abort()
    }
    
    // ÏÉàÎ°úÏö¥ Ïª®Ìä∏Î°§Îü¨ ÏÉùÏÑ±
    abortControllerRef.current = new AbortController()
    return abortControllerRef.current
  }, [])

  // ÌòÑÏû¨ AbortController Í∞ÄÏ†∏Ïò§Í∏∞ (ÏóÜÏúºÎ©¥ ÏÉùÏÑ±)
  const getAbortController = useCallback(() => {
    if (!abortControllerRef.current) {
      abortControllerRef.current = new AbortController()
    }
    return abortControllerRef.current
  }, [])

  // ÏöîÏ≤≠ Ï∑®ÏÜå
  const abort = useCallback(() => {
    if (abortControllerRef.current) {
      abortControllerRef.current.abort()
      abortControllerRef.current = null
    }
  }, [])

  // Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ï†ïÎ¶¨
  useEffect(() => {
    return () => {
      if (abortControllerRef.current) {
        abortControllerRef.current.abort()
      }
    }
  }, [])

  return {
    createAbortController,
    getAbortController,
    abort,
    signal: abortControllerRef.current?.signal
  }
}

/**
 * API ÏöîÏ≤≠ÏùÑ ÏúÑÌïú AbortControllerÎ•º ÏûêÎèôÏúºÎ°ú Í¥ÄÎ¶¨ÌïòÎäî Hook
 * ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥Ïù¥ Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ Ïù¥Ï†Ñ ÏöîÏ≤≠ÏùÑ Ï∑®ÏÜåÌïòÍ≥† ÏÉàÎ°úÏö¥ ÏöîÏ≤≠ÏùÑ ÏãúÏûëÌï©ÎãàÎã§.
 */
export function useAbortableEffect(
  effect: (signal: AbortSignal) => void | (() => void),
  deps: React.DependencyList = []
) {
  const { createAbortController } = useAbortController()

  useEffect(() => {
    const abortController = createAbortController()
    
    const cleanup = effect(abortController.signal)
    
    return () => {
      if (typeof cleanup === 'function') {
        cleanup()
      }
      abortController.abort()
    }
  }, deps)
}

/**
 * API ÏöîÏ≤≠ÏùÑ ÏúÑÌïú fetch wrapper with AbortController
 */
export async function fetchWithAbort(
  url: string,
  options: RequestInit = {},
  signal?: AbortSignal
): Promise<Response> {
  const response = await fetch(url, {
    ...options,
    signal: signal || null
  })

  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`)
  }

  return response
}

export default useAbortController
