import React, { useState, useCallback } from 'react'
import { FaPlus } from 'react-icons/fa'
import { SkillCategory, Skill, SkillCategoryFormData, SkillFormData } from '../../types'
import { useSkillsManagerData } from '../../hooks/useSkillsManagerData'
import SkillCategoryForm from '../forms/SkillCategoryForm'
import SkillForm from '../forms/SkillForm'
import SortableCategoryCard from './SortableCategoryCard'
import SortableSkillItem from './SortableSkillItem'
import { DndContext, closestCenter, DragEndEvent } from '@dnd-kit/core'
import { SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable'
import { arrayMove } from '@dnd-kit/sortable'

const SkillsManager: React.FC = () => {
  const { 
    skillCategories, 
    isLoading, 
    createSkillCategory,
    updateSkillCategory,
    deleteSkillCategory,
    createSkill,
    updateSkill,
    deleteSkill
  } = useSkillsManagerData()


  // Îã®Ïùº Ìèº ÏÉÅÌÉú Í¥ÄÎ¶¨ - Ìïú Î≤àÏóê ÌïòÎÇòÏùò ÏûëÏóÖÎßå Í∞ÄÎä•
  const [activeForm, setActiveForm] = useState<'category' | 'skill' | null>(null)
  const [editingCategory, setEditingCategory] = useState<SkillCategory | null>(null)
  const [editingSkill, setEditingSkill] = useState<Skill | null>(null)
  const [selectedCategoryId, setSelectedCategoryId] = useState<string | null>(null)
  const [expandedCategories, setExpandedCategories] = useState<Set<string>>(new Set())
  const [isSaving, setIsSaving] = useState(false)
  const [isDeleting, setIsDeleting] = useState(false)

  // üåü ÏàôÎ†®ÎèÑ Î°úÏª¨ ÏÉÅÌÉú Í¥ÄÎ¶¨ (Î≥ÄÍ≤ΩÏÇ¨Ìï≠ Ï∂îÏ†ÅÏö©)
  const [localLevels, setLocalLevels] = useState<Map<string, number>>(new Map())
  
  // üåü Ï†ÄÏû• Ï§ëÏù∏ Ïä§ÌÇ¨ ID Ï∂îÏ†Å
  const [savingSkillIds, setSavingSkillIds] = useState<Set<string>>(new Set())

  // üåü Ïä¨ÎùºÏù¥Îçî Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨ (Ï¶âÏãú Ï†ÄÏû• ÏóÜÏù¥ Î°úÏª¨ ÏÉÅÌÉúÎßå ÏóÖÎç∞Ïù¥Ìä∏)
  const handleLevelChange = useCallback((skillId: string, newLevel: number) => {
    setLocalLevels(prev => {
      const newMap = new Map(prev)
      newMap.set(skillId, newLevel)
      return newMap
    })
  }, [])

  // üåü Í∞úÎ≥Ñ Ïä§ÌÇ¨ ÏàôÎ†®ÎèÑ Ï†ÄÏû• Ìï∏Îì§Îü¨
  const handleSaveLevel = useCallback(async (skill: Skill) => {
    const skillId = skill._id!
    const newLevel = localLevels.get(skillId)
    
    if (newLevel === undefined || newLevel === skill.level) {
      // Î≥ÄÍ≤ΩÏÇ¨Ìï≠Ïù¥ ÏóÜÏúºÎ©¥ Î°úÏª¨ ÏÉÅÌÉúÎßå Ï†ïÎ¶¨
      setLocalLevels(prev => {
        const newMap = new Map(prev)
        newMap.delete(skillId)
        return newMap
      })
      return
    }

    try {
      setSavingSkillIds(prev => new Set(prev).add(skillId))
      console.log(`üíæ ÏàôÎ†®ÎèÑ Ï†ÄÏû•: ${skill.name} = ${newLevel}%`)
      
      await updateSkill(skillId, { 
        name: skill.name,
        icon: skill.icon,
        level: newLevel,
        order: skill.order,
        ...(skill.description && { description: skill.description }),
        ...(skill.color && { color: skill.color }),
        ...(skill.showInSidebar !== undefined && { showInSidebar: skill.showInSidebar }),
        ...(skill.showInLanguageCard !== undefined && { showInLanguageCard: skill.showInLanguageCard })
      })
      
      console.log(`‚úÖ Ï†ÄÏû• ÏôÑÎ£å: ${skill.name}`)
      
      // Ï†ÄÏû• ÏÑ±Í≥µ ÌõÑ Î°úÏª¨ ÏÉÅÌÉú Ï†ïÎ¶¨
      setLocalLevels(prev => {
        const newMap = new Map(prev)
        newMap.delete(skillId)
        return newMap
      })
    } catch (error) {
      console.error(`‚ùå Ï†ÄÏû• Ïã§Ìå®: ${skill.name}`, error)
      alert(`Ï†ÄÏû• Ïã§Ìå®: ${skill.name}`)
    } finally {
      setSavingSkillIds(prev => {
        const newSet = new Set(prev)
        newSet.delete(skillId)
        return newSet
      })
    }
  }, [localLevels, updateSkill])

  // üåü ÏÇ¨Ïù¥ÎìúÎ∞î ÌëúÏãú ÌÜ†Í∏Ä Ìï∏Îì§Îü¨
  const handleToggleSidebar = useCallback(async (skill: Skill) => {
    if (!skill._id) return
    
    try {
      setSavingSkillIds(prev => new Set(prev).add(skill._id!))
      const newValue = !skill.showInSidebar
      console.log(`üíæ [ÌïµÏã¨Í∏∞Ïà†] ÌÜ†Í∏Ä: ${skill.name} = ${newValue}`)
      console.log(`üìã [ÌïµÏã¨Í∏∞Ïà†] Ï†ÑÏÜ° Îç∞Ïù¥ÌÑ∞:`, {
        name: skill.name,
        showInSidebar: newValue,
        showInLanguageCard: skill.showInLanguageCard
      })
      
      const result = await updateSkill(skill._id, {
        name: skill.name,
        icon: skill.icon,
        level: skill.level,
        order: skill.order,
        showInSidebar: newValue,
        ...(skill.description && { description: skill.description }),
        ...(skill.color && { color: skill.color }),
        ...(skill.showInLanguageCard !== undefined && { showInLanguageCard: skill.showInLanguageCard })
      })
      
      console.log(`‚úÖ [ÌïµÏã¨Í∏∞Ïà†] ÌÜ†Í∏Ä ÏôÑÎ£å:`, result)
    } catch (error) {
      console.error(`‚ùå [ÌïµÏã¨Í∏∞Ïà†] ÌÜ†Í∏Ä Ïã§Ìå®: ${skill.name}`, error)
      alert(`ÏÇ¨Ïù¥ÎìúÎ∞î ÌëúÏãú ÌÜ†Í∏Ä Ïã§Ìå®: ${skill.name}`)
    } finally {
      setSavingSkillIds(prev => {
        const newSet = new Set(prev)
        newSet.delete(skill._id!)
        return newSet
      })
    }
  }, [updateSkill])

  // üåü Ïñ∏Ïñ¥ Ïπ¥Îìú ÌëúÏãú ÌÜ†Í∏Ä Ìï∏Îì§Îü¨
  const handleToggleLanguageCard = useCallback(async (skill: Skill) => {
    if (!skill._id) return
    
    try {
      setSavingSkillIds(prev => new Set(prev).add(skill._id!))
      const newValue = !skill.showInLanguageCard
      console.log(`üíæ [Ïñ∏Ïñ¥Ïπ¥Îìú] ÌÜ†Í∏Ä: ${skill.name} = ${newValue}`)
      console.log(`üìã [Ïñ∏Ïñ¥Ïπ¥Îìú] Ï†ÑÏÜ° Îç∞Ïù¥ÌÑ∞:`, {
        name: skill.name,
        showInSidebar: skill.showInSidebar,
        showInLanguageCard: newValue
      })
      
      const result = await updateSkill(skill._id, {
        name: skill.name,
        icon: skill.icon,
        level: skill.level,
        order: skill.order,
        showInLanguageCard: newValue,
        ...(skill.description && { description: skill.description }),
        ...(skill.color && { color: skill.color }),
        ...(skill.showInSidebar !== undefined && { showInSidebar: skill.showInSidebar })
      })
      
      console.log(`‚úÖ [Ïñ∏Ïñ¥Ïπ¥Îìú] ÌÜ†Í∏Ä ÏôÑÎ£å:`, result)
    } catch (error) {
      console.error(`‚ùå [Ïñ∏Ïñ¥Ïπ¥Îìú] ÌÜ†Í∏Ä Ïã§Ìå®: ${skill.name}`, error)
      alert(`Ïñ∏Ïñ¥ Ïπ¥Îìú ÌëúÏãú ÌÜ†Í∏Ä Ïã§Ìå®: ${skill.name}`)
    } finally {
      setSavingSkillIds(prev => {
        const newSet = new Set(prev)
        newSet.delete(skill._id!)
        return newSet
      })
    }
  }, [updateSkill])

  // üåü Ïπ¥ÌÖåÍ≥†Î¶¨ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ìï∏Îì§Îü¨
  const handleCategoryDragEnd = useCallback(async (event: DragEndEvent) => {
    const { active, over } = event
    
    if (!over || active.id === over.id) return
    
    const oldIndex = skillCategories.findIndex(cat => cat._id === active.id)
    const newIndex = skillCategories.findIndex(cat => cat._id === over.id)
    
    if (oldIndex === -1 || newIndex === -1) return
    
    // Î∞∞Ïó¥ ÏàúÏÑú Î≥ÄÍ≤Ω
    const reorderedCategories = arrayMove(skillCategories, oldIndex, newIndex)
    
    // Í∞Å Ïπ¥ÌÖåÍ≥†Î¶¨Ïùò order Í∞íÏùÑ ÏÉàÎ°úÏö¥ Ïù∏Îç±Ïä§Î°ú ÏóÖÎç∞Ïù¥Ìä∏ÌïòÍ≥† Ï†ÄÏû•
    try {
      for (let i = 0; i < reorderedCategories.length; i++) {
        const category = reorderedCategories[i]
        if (!category?._id) continue
        
        await updateSkillCategory(category._id, {
          title: category.title,
          color: category.color,
          order: i,
          ...(category.description && { description: category.description })
        })
      }
      console.log('‚úÖ Ïπ¥ÌÖåÍ≥†Î¶¨ ÏàúÏÑú Î≥ÄÍ≤Ω ÏôÑÎ£å')
    } catch (error) {
      console.error('‚ùå Ïπ¥ÌÖåÍ≥†Î¶¨ ÏàúÏÑú Î≥ÄÍ≤Ω Ïã§Ìå®:', error)
      alert('ÏàúÏÑú Î≥ÄÍ≤ΩÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
    }
  }, [skillCategories, updateSkillCategory])

  // üåü Ïä§ÌÇ¨ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ìï∏Îì§Îü¨ (Ïπ¥ÌÖåÍ≥†Î¶¨Î≥Ñ)
  const handleSkillDragEnd = useCallback(async (event: DragEndEvent, categoryId: string) => {
    const { active, over } = event
    
    if (!over || active.id === over.id) return
    
    const category = skillCategories.find(cat => cat._id === categoryId)
    if (!category || !category.skills) return
    
    const oldIndex = category.skills.findIndex(skill => skill._id === active.id)
    const newIndex = category.skills.findIndex(skill => skill._id === over.id)
    
    if (oldIndex === -1 || newIndex === -1) return
    
    // Ïä§ÌÇ¨ Î∞∞Ïó¥ ÏàúÏÑú Î≥ÄÍ≤Ω
    const reorderedSkills = arrayMove(category.skills, oldIndex, newIndex)
    
    // Í∞Å Ïä§ÌÇ¨Ïùò order Í∞íÏùÑ ÏÉàÎ°úÏö¥ Ïù∏Îç±Ïä§Î°ú ÏóÖÎç∞Ïù¥Ìä∏ÌïòÍ≥† Ï†ÄÏû•
    try {
      for (let i = 0; i < reorderedSkills.length; i++) {
        const skill = reorderedSkills[i]
        if (!skill?._id) continue
        
        await updateSkill(skill._id, {
          name: skill.name,
          icon: skill.icon,
          level: skill.level,
          order: i,
          ...(skill.description && { description: skill.description }),
          ...(skill.color && { color: skill.color })
        })
      }
      console.log(`‚úÖ Ïä§ÌÇ¨ ÏàúÏÑú Î≥ÄÍ≤Ω ÏôÑÎ£å (Ïπ¥ÌÖåÍ≥†Î¶¨: ${category.title})`)
    } catch (error) {
      console.error('‚ùå Ïä§ÌÇ¨ ÏàúÏÑú Î≥ÄÍ≤Ω Ïã§Ìå®:', error)
      alert('ÏàúÏÑú Î≥ÄÍ≤ΩÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
    }
  }, [skillCategories, updateSkill])

  const toggleCategory = (categoryId: string) => {
    setExpandedCategories(prev => {
      const newSet = new Set(prev)
      if (newSet.has(categoryId)) {
        newSet.delete(categoryId)
      } else {
        newSet.add(categoryId)
      }
      return newSet
    })
  }

  const handleAddCategory = () => {
    setEditingCategory(null)
    setActiveForm('category')
  }

  const handleEditCategory = (category: SkillCategory) => {
    setEditingCategory(category)
    setActiveForm('category')
  }

  const handleSaveCategory = async (data: SkillCategoryFormData) => {
    try {
      setIsSaving(true)
      console.log('üîç Ïπ¥ÌÖåÍ≥†Î¶¨ Ï†ÄÏû• ÏãúÏûë:', data)
      
      if (editingCategory) {
        console.log('üìù Ïπ¥ÌÖåÍ≥†Î¶¨ ÏàòÏ†ï Î™®Îìú:', editingCategory._id)
        const result = await updateSkillCategory(editingCategory._id!, data)
        console.log('‚úÖ ÏàòÏ†ï Í≤∞Í≥º:', result)
        if (result.success) {
          setActiveForm(null)
          setEditingCategory(null)
          alert('Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.')
        } else {
          alert(`Ïπ¥ÌÖåÍ≥†Î¶¨ ÏàòÏ†ï Ïã§Ìå®: ${result.error || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'}`)
        }
      } else {
        console.log('‚ûï Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÉùÏÑ± Î™®Îìú')
        const result = await createSkillCategory(data)
        console.log('‚úÖ ÏÉùÏÑ± Í≤∞Í≥º:', result)
        if (result.success) {
          setActiveForm(null)
          alert('Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§.')
        } else {
          alert(`Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä Ïã§Ìå®: ${result.error || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'}`)
        }
      }
    } catch (error) {
      console.error('‚ùå Ïπ¥ÌÖåÍ≥†Î¶¨ Ï†ÄÏû• Ïã§Ìå®:', error)
      alert(`Ïπ¥ÌÖåÍ≥†Î¶¨ Ï†ÄÏû• Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§: ${error}`)
    } finally {
      setIsSaving(false)
    }
  }

  const handleDeleteCategory = async (category: SkillCategory) => {
    if (confirm('Ï†ïÎßê ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå? Ïπ¥ÌÖåÍ≥†Î¶¨ ÎÇ¥ Î™®Îì† Ïä§ÌÇ¨ÎèÑ Ìï®Íªò ÏÇ≠Ï†úÎê©ÎãàÎã§.')) {
      try {
        setIsDeleting(true)
        await deleteSkillCategory(category._id!)
      } catch (error) {
        console.error('Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      } finally {
        setIsDeleting(false)
      }
    }
  }

  const handleAddSkill = (categoryId: string) => {
    setSelectedCategoryId(categoryId)
    setEditingSkill(null)
    setActiveForm('skill')
  }

  const handleEditSkill = (skill: Skill) => {
    setEditingSkill(skill)
    setSelectedCategoryId(skill.categoryId)
    setActiveForm('skill')
  }

  const handleSaveSkill = async (data: SkillFormData) => {
    if (!selectedCategoryId && !editingSkill) return
    
    try {
      setIsSaving(true)
      const categoryId = editingSkill?.categoryId || selectedCategoryId!
      
      if (editingSkill) {
        const result = await updateSkill(editingSkill._id!, data)
        if (result.success) {
          setActiveForm(null)
          setEditingSkill(null)
          setSelectedCategoryId(null)
        }
      } else {
        const result = await createSkill(categoryId, data)
        if (result.success) {
          setActiveForm(null)
          setSelectedCategoryId(null)
        }
      }
    } catch (error) {
      console.error('Ïä§ÌÇ¨ Ï†ÄÏû• Ïã§Ìå®:', error)
    } finally {
      setIsSaving(false)
    }
  }

  const handleDeleteSkill = async (skill: Skill) => {
    if (confirm('Ï†ïÎßê ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) {
      try {
        setIsDeleting(true)
        await deleteSkill(skill._id!)
      } catch (error) {
        console.error('Ïä§ÌÇ¨ ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      } finally {
        setIsDeleting(false)
      }
    }
  }

  const handleCancel = () => {
    setActiveForm(null)
    setEditingCategory(null)
    setEditingSkill(null)
    setSelectedCategoryId(null)
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center py-8">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        <span className="ml-2">Í∏∞Ïà† Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</span>
      </div>
    )
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold">Í∏∞Ïà† Í¥ÄÎ¶¨</h2>
        <button
          onClick={handleAddCategory}
          className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex items-center gap-2"
        >
          <FaPlus />
          Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä
        </button>
      </div>

      {/* Ïπ¥ÌÖåÍ≥†Î¶¨ Ìèº */}
      {activeForm === 'category' && (
        <SkillCategoryForm
          initialData={editingCategory}
          onSave={handleSaveCategory}
          onCancel={handleCancel}
          isSaving={isSaving}
        />
      )}

      {/* Ïä§ÌÇ¨ Ìèº */}
      {activeForm === 'skill' && (
        <SkillForm
          initialData={editingSkill}
          categoryId={selectedCategoryId || undefined}
          onSave={handleSaveSkill}
          onCancel={handleCancel}
          isSaving={isSaving}
        />
      )}

      {/* Ïπ¥ÌÖåÍ≥†Î¶¨ Î™©Î°ù - üåü ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ï†ÅÏö© */}
      <DndContext collisionDetection={closestCenter} onDragEnd={handleCategoryDragEnd}>
        <SortableContext items={skillCategories.map(cat => cat._id!)} strategy={verticalListSortingStrategy}>
          <div className="space-y-4">
            {skillCategories.map((category) => {
              const isExpanded = expandedCategories.has(category._id!)
              return (
                <SortableCategoryCard
                  key={category._id}
                  category={category}
                  isExpanded={isExpanded}
                  onToggle={toggleCategory}
                  onAddSkill={handleAddSkill}
                  onEdit={handleEditCategory}
                  onDelete={handleDeleteCategory}
                  isSaving={isSaving}
                  isDeleting={isDeleting}
                >
              
                  {/* Ïä§ÌÇ¨ Î™©Î°ù - üåü ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ï†ÅÏö© */}
                  <div className="border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-800">
                    {category.skills && category.skills.length > 0 ? (
                      <DndContext 
                        collisionDetection={closestCenter} 
                        onDragEnd={(event) => handleSkillDragEnd(event, category._id!)}
                      >
                        <SortableContext 
                          items={category.skills.map(skill => skill._id!)} 
                          strategy={verticalListSortingStrategy}
                        >
                          <div className="p-4 space-y-2">
                            {category.skills
                              .sort((a, b) => a.order - b.order)
                              .map((skill) => {
                                const currentLevel = localLevels.get(skill._id!) ?? skill.level
                                const hasChanges = localLevels.has(skill._id!) && localLevels.get(skill._id!) !== skill.level
                                const isSavingThis = savingSkillIds.has(skill._id!)
                                
                                return (
                                  <SortableSkillItem
                                    key={skill._id}
                                    skill={skill}
                                    categoryColor={category.color || '#3B82F6'}
                                    currentLevel={currentLevel}
                                    hasChanges={hasChanges}
                                    isSavingThis={isSavingThis}
                                    isSaving={isSaving}
                                    isDeleting={isDeleting}
                                    onLevelChange={handleLevelChange}
                                    onSaveLevel={handleSaveLevel}
                                    onEdit={handleEditSkill}
                                    onDelete={handleDeleteSkill}
                                    onToggleSidebar={handleToggleSidebar}
                                    onToggleLanguageCard={handleToggleLanguageCard}
                                  />
                                )
                              })}
                          </div>
                        </SortableContext>
                      </DndContext>
                    ) : (
                      <div className="p-4 text-center text-gray-500 dark:text-gray-400">
                        <FaPlus size={32} className="text-blue-400 dark:text-blue-500 mx-auto mb-2" />
                        <p className="text-sm">ÏïÑÏßÅ Ïä§ÌÇ¨Ïù¥ ÏóÜÏäµÎãàÎã§.</p>
                        <p className="text-xs mt-1">Ïπ¥ÌÖåÍ≥†Î¶¨ Ï†úÎ™© ÏòÜÏùò + Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌïòÏó¨ Ïä§ÌÇ¨ÏùÑ Ï∂îÍ∞ÄÌïòÏÑ∏Ïöî.</p>
                      </div>
                    )}
                  </div>
                </SortableCategoryCard>
              )
            })}
          </div>
        </SortableContext>
      </DndContext>

      {skillCategories.length === 0 && (
        <div className="text-center py-8 text-gray-500 dark:text-gray-400">
          <p className="text-lg mb-2">ÏïÑÏßÅ ÏÉùÏÑ±Îêú Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏóÜÏäµÎãàÎã§.</p>
          <p>ÏúÑÏùò "Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä" Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌïòÏó¨ Ï≤´ Î≤àÏß∏ Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÎßåÎì§Ïñ¥Î≥¥ÏÑ∏Ïöî.</p>
        </div>
      )}
    </div>
  )
}

export default SkillsManager
