import { useState, useEffect, useCallback } from 'react'
import { experiencesAPI } from '../services/api'
import { Experience } from '../types'

export const useExperienceManagerData = () => {
  const [experiences, setExperiences] = useState<Experience[]>([])
  const [isLoading, setIsLoading] = useState(false)

  // Experience Îç∞Ïù¥ÌÑ∞Îßå Í∞ÄÏ†∏Ïò§Îäî Ìï®Ïàò
  const fetchExperiences = useCallback(async () => {
    setIsLoading(true)
    try {
      const response = await experiencesAPI.getAll()
        setExperiences(response.data.data || [])
    } catch (error) {
      console.error('Experience Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ïã§Ìå®:', error)
    } finally {
      setIsLoading(false)
    }
  }, [])

  // Experience ÏÉùÏÑ±
  const createExperience = useCallback(async (experienceData: any) => {
    try {
      await experiencesAPI.create(experienceData)
      await fetchExperiences() // ÏÉàÎ°úÍ≥†Ïπ®
      return { success: true }
    } catch (error) {
      console.error('Experience ÏÉùÏÑ± Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }, [fetchExperiences])

  // Experience ÏàòÏ†ï
  const updateExperience = useCallback(async (id: string, experienceData: any) => {
    try {
      await experiencesAPI.update(id, experienceData)
      await fetchExperiences() // ÏÉàÎ°úÍ≥†Ïπ®
      return { success: true }
    } catch (error) {
      console.error('Experience ÏàòÏ†ï Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }, [fetchExperiences])

  // Experience ÏÇ≠Ï†ú
  const deleteExperience = useCallback(async (id: string) => {
    try {
      await experiencesAPI.delete(id)
      await fetchExperiences() // ÏÉàÎ°úÍ≥†Ïπ®
      return { success: true }
    } catch (error) {
      console.error('Experience ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }, [fetchExperiences])

  useEffect(() => {
    fetchExperiences()
  }, [])

  return {
    experiences,
    isLoading,
    fetchExperiences,
    createExperience,
    updateExperience,
    deleteExperience
  }
}
