import { useQuery, useQueryClient } from '@tanstack/react-query'
import { booksAPI, videoLearningsAPI, videoPlaylistsAPI, experiencesAPI, projectsAPI, messagesAPI, skillsAPI, categoriesAPI } from '../services/api'
import { MESSAGE_QUERY_KEY } from './useMessageManagerData'

interface DataCounts {
  books: number
  videoLearnings: number
  videoPlaylists: number
  experiences: number
  projects: number
  messages: number
  skills: number
  categories: number
}

// üåü React QueryÎ•º ÏÇ¨Ïö©Ìïú Îç∞Ïù¥ÌÑ∞ Ïπ¥Ïö¥Ìä∏ Ï°∞Ìöå
export const useDataCounts = () => {
  const queryClient = useQueryClient()
  
  const { data: counts, isLoading } = useQuery<DataCounts, Error>({
    queryKey: ['dataCounts'], // üí° ÎèÖÎ¶ΩÏ†ÅÏù∏ ÏøºÎ¶¨ ÌÇ§
    queryFn: async () => {
      // Ïã§Ï†úÎ°úÎäî Í∞Å APIÏóêÏÑú Îç∞Ïù¥ÌÑ∞ Í∞úÏàòÎßå Î∞òÌôòÌïòÎäî Ï†ÑÏö© ÏóîÎìúÌè¨Ïù∏Ìä∏Î•º Ìò∏Ï∂úÌïòÎäî Í≤ÉÏù¥ Ìö®Ïú®Ï†Å
      // ÌòÑÏû¨Îäî Í∏∞Ï°¥ APIÎ•º ÏÇ¨Ïö©ÌïòÏó¨ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò® ÌõÑ lengthÎ•º Í≥ÑÏÇ∞
      const [booksResponse, videoLearningsResponse, videoPlaylistsResponse, experiencesResponse, projectsResponse, messagesResponse, skillsResponse, categoriesResponse] = await Promise.all([
        booksAPI.getAll(),
        videoLearningsAPI.getAll(),
        videoPlaylistsAPI.getAll(),
        experiencesAPI.getAll(),
        projectsAPI.getAll(),
        messagesAPI.getAll(), // üí° Î©îÏãúÏßÄÎèÑ ÏßÅÏ†ë Ï°∞Ìöå
        skillsAPI.getCategories(),
        categoriesAPI.getAll()
      ])
      
      return {
        books: booksResponse.data?.data?.length || 0,
        videoLearnings: videoLearningsResponse.data?.data?.length || 0,
        videoPlaylists: videoPlaylistsResponse.data?.data?.length || 0,
        experiences: experiencesResponse.data?.data?.length || 0,
        projects: projectsResponse.data?.data?.length || 0,
        messages: messagesResponse.data?.data?.length || 0, // üí° Î©îÏãúÏßÄ API ÏùëÎãµÏóêÏÑú Í∏∏Ïù¥ Í≥ÑÏÇ∞
        skills: skillsResponse.data?.data?.length || 0,
        categories: categoriesResponse.data?.length || 0,
      }
    },
    staleTime: 2 * 60 * 1000, // 2Î∂Ñ ÎèôÏïà Ï∫êÏãú Ïú†ÏßÄ
    gcTime: 5 * 60 * 1000, // 5Î∂Ñ ÌõÑ Í∞ÄÎπÑÏßÄ Ïª¨Î†âÏÖò
    refetchOnWindowFocus: true, // Ï∞Ω Ìè¨Ïª§Ïä§ Ïãú ÏûêÎèô ÏÉàÎ°úÍ≥†Ïπ® (Îç∞Ïù¥ÌÑ∞ ÎèôÍ∏∞Ìôî)
  })

  return { 
    counts: counts || {
      books: 0,
      videoLearnings: 0,
      videoPlaylists: 0,
      experiences: 0,
      projects: 0,
      messages: 0,
      skills: 0,
      categories: 0,
    }, 
    isLoading,
    refreshCounts: () => {} // Îçî Ïù¥ÏÉÅ ÌïÑÏöî ÏóÜÏßÄÎßå Ìò∏ÌôòÏÑ± Ïú†ÏßÄ
  }
}

export default useDataCounts
