interface LearningFormProps {
  learning?: Learning | null
  onSave: (data: { topic: string; content: string }) => Promise<void>
  onCancel: () => void
}

const LearningForm: React.FC<LearningFormProps> = ({
  learning,
  onSave,
  onCancel
}) => {
  const [topic, setTopic] = useState('')
  const [content, setContent] = useState('')
  const [isSaving, setIsSaving] = useState(false)

  // Quill ÏóêÎîîÌÑ∞ ÏÑ§Ï†ï
  const quillModules = {
    toolbar: [
      [{ 'header': [1, 2, 3, false] }],
      ['bold', 'italic', 'underline', 'strike'],
      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'indent': '-1'}, { 'indent': '+1' }],
      ['blockquote', 'code-block'],
      [{ 'color': [] }, { 'background': [] }],
      ['link', 'image'],
      ['clean']
    ]
  }

  const quillFormats = [
    'header', 'bold', 'italic', 'underline', 'strike',
    'list', 'bullet', 'indent',
    'blockquote', 'code-block',
    'color', 'background',
    'link', 'image'
  ]

  useEffect(() => {
    if (learning) {
      setTopic(learning.topic)
      setContent(learning.content)
    } else {
      setTopic('')
      setContent('')
    }
  }, [learning])

  // HTML ÌÉúÍ∑∏Î•º Ï†úÍ±∞ÌïòÍ≥† ÏàúÏàò ÌÖçÏä§Ìä∏Îßå Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
  const stripHtml = (html: string) => {
    const tmp = document.createElement('div')
    tmp.innerHTML = html
    return tmp.textContent || tmp.innerText || ''
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!topic.trim()) {
      alert('ÌïôÏäµ Ï£ºÏ†úÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
      return
    }

    // HTML ÌÉúÍ∑∏Î•º Ï†úÍ±∞Ìïú ÏàúÏàò ÌÖçÏä§Ìä∏Î°ú Í≤ÄÏ¶ù
    const plainTextContent = stripHtml(content).trim()
    if (!plainTextContent) {
      alert('ÌïôÏäµ ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
      return
    }

    try {
      setIsSaving(true)
      await onSave({ topic: topic.trim(), content: content.trim() })
    } catch (error) {
      console.error('ÌïôÏäµ ÎÇ¥Ïö© Ï†ÄÏû• Ïã§Ìå®:', error)
    } finally {
      setIsSaving(false)
    }
  }

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-6">
      <div className="flex items-center justify-between mb-4">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
          {learning ? 'ÌïôÏäµ ÎÇ¥Ïö© ÏàòÏ†ï' : 'ÌïôÏäµ ÎÇ¥Ïö© Ï∂îÍ∞Ä'}
        </h3>
        <button
          onClick={onCancel}
          className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
        >
          <FaTimes className="text-gray-500 dark:text-gray-400" />
        </button>
      </div>

      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="topic" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            ÌïôÏäµ Ï£ºÏ†ú
          </label>
          <input
            type="text"
            id="topic"
            value={topic}
            onChange={(e) => setTopic(e.target.value)}
            placeholder="ÌïôÏäµ Ï£ºÏ†úÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white dark:bg-gray-700 text-gray-900 dark:text-white"
            required
          />
        </div>

        <div>
          <label htmlFor="content" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            ÌïôÏäµ ÎÇ¥Ïö©
            <span className="text-xs text-gray-500 ml-2">
              (ÎßàÌÅ¨Îã§Ïö¥ Î¨∏Î≤ï ÏßÄÏõê: **ÍµµÍ≤å**, *Í∏∞Ïö∏ÏûÑ*, # Ï†úÎ™©, - Î™©Î°ù)
            </span>
          </label>
          <div className="rich-text-editor-container">
            <ReactQuill
              theme="snow"
              value={content}
              onChange={(value) => {
                console.log('ReactQuill onChange:', value)
                setContent(value)
              }}
              placeholder="ÌïôÏäµ ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî. ÎßàÌÅ¨Îã§Ïö¥ Î¨∏Î≤ïÏùÑ ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§."
              modules={quillModules}
              formats={quillFormats}
              className="rich-text-editor"
            />
          </div>
        </div>

        <div className="flex gap-3 pt-4">
          <button
            type="submit"
            disabled={isSaving || !topic.trim() || !stripHtml(content).trim()}
            className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors flex items-center justify-center gap-2"
          >
            <FaSave />
            {isSaving ? 'Ï†ÄÏû• Ï§ë...' : (learning ? 'ÏàòÏ†ï' : 'Ï∂îÍ∞Ä')}
          </button>
          <button
            type="button"
            onClick={onCancel}
            className="px-4 py-2 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition-colors"
          >
            Ï∑®ÏÜå
          </button>
        </div>
      </form>
    </div>
  )
}

export default LearningForm
