import { useState, useEffect, useCallback } from 'react'
import { projectsAPI } from '../services/api'
import { Project } from '../types'

export const useProjectManagerData = () => {
  const [projects, setProjects] = useState<Project[]>([])
  const [isLoading, setIsLoading] = useState(false)

  // Project Îç∞Ïù¥ÌÑ∞Îßå Í∞ÄÏ†∏Ïò§Îäî Ìï®Ïàò
  const fetchProjects = useCallback(async () => {
    setIsLoading(true)
    try {
      console.log('üì• ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞...')
      const response = await projectsAPI.getAll()
      const projectsData = response.data.data || []
      console.log(`‚úÖ ${projectsData.length}Í∞ú ÌîÑÎ°úÏ†ùÌä∏ Î°úÎìúÎê®`)
      console.log('üìä ÌîÑÎ°úÏ†ùÌä∏ Îç∞Ïù¥ÌÑ∞:', projectsData)
      setProjects(projectsData)
    } catch (error) {
      console.error('‚ùå Project Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ïã§Ìå®:', error)
    } finally {
      setIsLoading(false)
    }
  }, [])

  // Project ÏÉùÏÑ±
  const createProject = useCallback(async (projectData: any) => {
    try {
      console.log('‚ûï ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ± API Ìò∏Ï∂ú:', projectData)
      const response = await projectsAPI.create(projectData)
      console.log('‚úÖ ÏÑúÎ≤Ñ ÏùëÎãµ:', response.data)
      console.log('üîÑ ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ® ÏãúÏûë...')
      await fetchProjects() // ÏÉàÎ°úÍ≥†Ïπ®
      console.log('‚úÖ ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ® ÏôÑÎ£å')
      return { success: true }
    } catch (error) {
      console.error('‚ùå Project ÏÉùÏÑ± Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }, [fetchProjects])

  // Project ÏàòÏ†ï
  const updateProject = useCallback(async (id: string, projectData: any) => {
    try {
      console.log('üîÑ ÌîÑÎ°úÏ†ùÌä∏ ÏàòÏ†ï API Ìò∏Ï∂ú:', { id, projectData })
      const response = await projectsAPI.update(id, projectData)
      console.log('‚úÖ ÏÑúÎ≤Ñ ÏùëÎãµ:', response.data)
      console.log('üîÑ ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ® ÏãúÏûë...')
      await fetchProjects() // ÏÉàÎ°úÍ≥†Ïπ®
      console.log('‚úÖ ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ® ÏôÑÎ£å')
      return { success: true }
    } catch (error) {
      console.error('‚ùå Project ÏàòÏ†ï Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }, [fetchProjects])

  // Project ÏÇ≠Ï†ú
  const deleteProject = useCallback(async (id: string) => {
    try {
      await projectsAPI.delete(id)
      await fetchProjects() // ÏÉàÎ°úÍ≥†Ïπ®
      return { success: true }
    } catch (error) {
      console.error('Project ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }, [fetchProjects])

  useEffect(() => {
    fetchProjects()
  }, [])

  return {
    projects,
    isLoading,
    fetchProjects,
    createProject,
    updateProject,
    deleteProject
  }
}
