import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { messagesAPI } from '../services/api'
import { ContactMessage } from '../types'

// üí° Î©îÏãúÏßÄ Í¥ÄÎ†® ÏøºÎ¶¨ ÌÇ§ ÏÉÅÏàò - Î™®Îì† Î©îÏãúÏßÄ Îç∞Ïù¥ÌÑ∞ Ï∫êÏãúÏùò Í∏∞Ï§ÄÏ†ê
export const MESSAGE_QUERY_KEY = 'adminMessages'

// üåü Î©îÏãúÏßÄ Î™©Î°ù Ï°∞Ìöå (React Query)
export const useMessages = () => {
  return useQuery<ContactMessage[], Error>({
    queryKey: [MESSAGE_QUERY_KEY, 'list'],
    queryFn: async () => {
      const response = await messagesAPI.getAll()
      return response.data.data || response.data || []
    },
    staleTime: 2 * 60 * 1000, // 2Î∂Ñ ÎèôÏïà Ï∫êÏãú Ïú†ÏßÄ
    gcTime: 5 * 60 * 1000, // 5Î∂Ñ ÌõÑ Í∞ÄÎπÑÏßÄ Ïª¨Î†âÏÖò
  })
}

// üåü Î©îÏãúÏßÄ ÏùΩÏùå Ï≤òÎ¶¨ (React Query Mutation)
export const useMarkMessageAsRead = () => {
  const queryClient = useQueryClient()

  return useMutation<void, Error, string>({
    mutationFn: async (id: string) => {
      await messagesAPI.markAsRead(id)
    },
    onSuccess: () => {
      // üí° ÌïµÏã¨: Î©îÏãúÏßÄ Í¥ÄÎ†® Î™®Îì† ÏøºÎ¶¨ Î¨¥Ìö®Ìôî ‚Üí ÏûêÎèôÏúºÎ°ú Îã§Ïãú fetch
      queryClient.invalidateQueries({ queryKey: [MESSAGE_QUERY_KEY] })
      // üí° Îç∞Ïù¥ÌÑ∞ Ïπ¥Ïö¥Ìä∏ÎèÑ Î¨¥Ìö®ÌôîÌïòÏó¨ Î©îÏãúÏßÄ Í∞úÏàò ÏóÖÎç∞Ïù¥Ìä∏
      queryClient.invalidateQueries({ queryKey: ['dataCounts'] })
    },
  })
}

// üåü Î©îÏãúÏßÄ ÏÇ≠Ï†ú (React Query Mutation)
export const useDeleteMessage = () => {
  const queryClient = useQueryClient()

  return useMutation<void, Error, string>({
    mutationFn: async (id: string) => {
      await messagesAPI.delete(id)
    },
    onSuccess: () => {
      // üí° ÌïµÏã¨: Î©îÏãúÏßÄ Í¥ÄÎ†® Î™®Îì† ÏøºÎ¶¨ Î¨¥Ìö®Ìôî ‚Üí ÏûêÎèôÏúºÎ°ú Îã§Ïãú fetch
      queryClient.invalidateQueries({ queryKey: [MESSAGE_QUERY_KEY] })
      // üí° Îç∞Ïù¥ÌÑ∞ Ïπ¥Ïö¥Ìä∏ÎèÑ Î¨¥Ìö®ÌôîÌïòÏó¨ Î©îÏãúÏßÄ Í∞úÏàò ÏóÖÎç∞Ïù¥Ìä∏
      queryClient.invalidateQueries({ queryKey: ['dataCounts'] })
    },
  })
}

// üåü ÏùºÍ¥Ñ ÏùΩÏùå Ï≤òÎ¶¨ (React Query Mutation)
export const useBulkMarkAsRead = () => {
  const queryClient = useQueryClient()

  return useMutation<void, Error, string[]>({
    mutationFn: (ids: string[]) => Promise.all(ids.map(id => messagesAPI.markAsRead(id))).then(() => undefined),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [MESSAGE_QUERY_KEY] })
      // üí° Îç∞Ïù¥ÌÑ∞ Ïπ¥Ïö¥Ìä∏ÎèÑ Î¨¥Ìö®ÌôîÌïòÏó¨ Î©îÏãúÏßÄ Í∞úÏàò ÏóÖÎç∞Ïù¥Ìä∏
      queryClient.invalidateQueries({ queryKey: ['dataCounts'] })
    },
  })
}

// üåü ÏùºÍ¥Ñ ÏÇ≠Ï†ú (React Query Mutation)
export const useBulkDeleteMessages = () => {
  const queryClient = useQueryClient()

  return useMutation<void, Error, string[]>({
    mutationFn: (ids: string[]) => Promise.all(ids.map(id => messagesAPI.delete(id))).then(() => undefined),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [MESSAGE_QUERY_KEY] })
      // üí° Îç∞Ïù¥ÌÑ∞ Ïπ¥Ïö¥Ìä∏ÎèÑ Î¨¥Ìö®ÌôîÌïòÏó¨ Î©îÏãúÏßÄ Í∞úÏàò ÏóÖÎç∞Ïù¥Ìä∏
      queryClient.invalidateQueries({ queryKey: ['dataCounts'] })
    },
  })
}

// üîÑ Í∏∞Ï°¥ ÌõÖ Ìò∏ÌôòÏÑ± Ïú†ÏßÄ (Í∏∞Ï°¥ Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä Î∞îÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎèÑÎ°ù)
export const useMessageManagerData = () => {
  const { data: messages = [], isLoading } = useMessages()
  const markAsReadMutation = useMarkMessageAsRead()
  const deleteMessageMutation = useDeleteMessage()
  const bulkMarkAsReadMutation = useBulkMarkAsRead()
  const bulkDeleteMutation = useBulkDeleteMessages()

  const markAsRead = async (id: string) => {
    try {
      await markAsReadMutation.mutateAsync(id)
      return { success: true }
    } catch (error) {
      console.error('Message ÏùΩÏùå Ï≤òÎ¶¨ Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }

  const deleteMessage = async (id: string) => {
    try {
      await deleteMessageMutation.mutateAsync(id)
      return { success: true }
    } catch (error) {
      console.error('Message ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }

  const bulkMarkAsRead = async (ids: string[]) => {
    try {
      await bulkMarkAsReadMutation.mutateAsync(ids)
      return { success: true }
    } catch (error) {
      console.error('Message ÏùºÍ¥Ñ ÏùΩÏùå Ï≤òÎ¶¨ Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }

  const bulkDelete = async (ids: string[]) => {
    try {
      await bulkDeleteMutation.mutateAsync(ids)
      return { success: true }
    } catch (error) {
      console.error('Message ÏùºÍ¥Ñ ÏÇ≠Ï†ú Ïã§Ìå®:', error)
      return { success: false, error }
    }
  }

  return {
    messages,
    isLoading,
    fetchMessages: () => {}, // Îçî Ïù¥ÏÉÅ ÌïÑÏöî ÏóÜÏßÄÎßå Ìò∏ÌôòÏÑ± Ïú†ÏßÄ
    markAsRead,
    deleteMessage,
    bulkMarkAsRead,
    bulkDelete
  }
}
