import React, { useState, useEffect, useMemo } from 'react'
import { useLocation } from 'react-router-dom'
import { useAuth } from '@/contexts/AuthContext'
import { FaSignOutAlt } from 'react-icons/fa'

// Î∂ÑÎ¶¨Îêú Ïª¥Ìè¨ÎÑåÌä∏Îì§ import
import DashboardHome from '@/components/admin/DashboardHome'
import BookManager from '@/components/admin/BookManager'
import VideoLearningManager from '@/components/admin/VideoLearningManager'
import VideoPlaylistManager from '@/components/admin/VideoPlaylistManager'
import ExperienceManager from '@/components/admin/ExperienceManager'
import ProjectManager from '@/components/admin/ProjectManager'
import MessageManager from '@/components/admin/MessageManager'
import SkillsManager from '@/components/admin/SkillsManager'
import CategoryManager from '@/components/admin/CategoryManager'
import NotificationDropdown from '@/components/common/NotificationDropdown'
import RightSidebar from '@/components/common/RightSidebar'
import SiteSettingsEditor from '@/admin/SiteSettingsEditor'
import ToastContainer from '@/components/common/Toast'
import useDataCounts from '@/hooks/useDataCounts'

// ÏÉàÎ°úÏö¥ Î¶¨Ìå©ÌÜ†ÎßÅÎêú ÌõÖ import
import { useAdminSidebarState, AdminTab } from '@/hooks/useAdminSidebarState'
import { useToastHelpers } from '@/hooks/useToast'
import { useMessages } from '@/hooks/useMessageManagerData'

const AdminDashboard: React.FC = () => {
  const { isAuthenticated, logout, isLoading } = useAuth()
  const location = useLocation()
  const [showNotifications, setShowNotifications] = useState(false)
  
  // Toast ÏãúÏä§ÌÖú Ï∂îÍ∞Ä
  const { toasts, hideToast } = useToastHelpers()
  
  // ÏÉàÎ°úÏö¥ Î¶¨Ìå©ÌÜ†ÎßÅÎêú ÏÉÅÌÉú Í¥ÄÎ¶¨ ÌõÖ ÏÇ¨Ïö©
  const {
    activeTab,
    setActiveTab,
    activeSettingsSection,
    setActiveSettingsSection,
    bookFilters,
    experienceFilters,
    projectFilters,
  } = useAdminSidebarState()
  
  // Îç∞Ïù¥ÌÑ∞ Ïπ¥Ïö¥Ìä∏ ÌõÖ ÏÇ¨Ïö©
  const { counts, refreshCounts } = useDataCounts()
  
  // üîî Î©îÏãúÏßÄ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏ÏôÄÏÑú ÏïåÎ¶ºÏúºÎ°ú Î≥ÄÌôò
  const { data: messages = [] } = useMessages()
  
  // üîî ÏùΩÏßÄ ÏïäÏùÄ Î©îÏãúÏßÄÎ•º ÏïåÎ¶º ÌòïÌÉúÎ°ú Î≥ÄÌôò
  const notifications = useMemo(() => {
    return messages
      .filter(msg => !msg.isRead)
      .map(msg => ({
        id: msg._id || '',
        message: `${msg.name}ÎãòÏúºÎ°úÎ∂ÄÌÑ∞ ÏÉàÎ°úÏö¥ Î©îÏãúÏßÄ: ${msg.subject}`,
        read: false,
        createdAt: msg.createdAt || new Date().toISOString()
      }))
      .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()) // ÏµúÏã†Ïàú Ï†ïÎ†¨
  }, [messages])

  // üåü URL stateÎ•º ÌÜµÌï¥ Ï†ÑÎã¨Îêú ÌÉ≠Í≥º ÏàòÏ†ï ID Ï≤òÎ¶¨
  useEffect(() => {
    const state = location.state as { tab?: AdminTab; editId?: string } | null
    if (state?.tab) {
      setActiveTab(state.tab)
    }
    // editIdÎäî Í∞Å Manager Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï≤òÎ¶¨
  }, [location.state, setActiveTab])

  // Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
  if (!isAuthenticated && !isLoading) {
    return <div>Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.</div>
  }

  // Î°úÎî© Ï§ëÏùº Îïå
  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600 dark:text-gray-400">Î°úÎî© Ï§ë...</p>
        </div>
      </div>
    )
  }

  // ÌÉ≠Î≥Ñ Ïª¥Ìè¨ÎÑåÌä∏ Î†åÎçîÎßÅ Ìï®Ïàò
  const renderActiveTabContent = () => {
    // üåü location.stateÏóêÏÑú editId Ï∂îÏ∂ú
    const state = location.state as { tab?: AdminTab; editId?: string } | null
    const editId = state?.editId
    
    switch (activeTab) {
      case 'books':
        return <BookManager 
          bookFilter={bookFilters.state.filter}
          bookSearchTerm={bookFilters.state.searchTerm}
          selectedBookCategory={bookFilters.state.selectedCategory}
          initialEditId={editId}  // üåü ÏàòÏ†ïÌï† ÏÑúÏ†Å ID Ï†ÑÎã¨
        />
      case 'videoLearnings':
        return <VideoLearningManager initialEditId={editId} />  // üåü ÏàòÏ†ïÌï† ÏòÅÏÉÅ ID Ï†ÑÎã¨
      case 'videoPlaylists':
        return <VideoPlaylistManager />
      case 'experiences':
        return <ExperienceManager 
          experienceFilter={experienceFilters.state.filter}
          experienceSearchTerm={experienceFilters.state.searchTerm}
          selectedExperienceCompany={experienceFilters.state.selectedCompany}
          selectedExperienceYear={experienceFilters.state.selectedYear}
        />
      case 'projects':
        return <ProjectManager 
          projectFilter={projectFilters.state.filter}
          projectSearchTerm={projectFilters.state.searchTerm}
          selectedProjectCategory={projectFilters.state.selectedCategory}
        />
      case 'messages':
        return <MessageManager />
      case 'skills':
        return <SkillsManager />
      case 'categories':
        return <CategoryManager />
      default: // 'settings'
        // activeSettingsSectionÏù¥ ÏûàÏúºÎ©¥ SiteSettingsEditorÎ•º, ÏóÜÏúºÎ©¥ DashboardHomeÏùÑ ÌëúÏãú
        return activeSettingsSection 
          ? <SiteSettingsEditor activeSection={activeSettingsSection} />
          : <DashboardHome counts={counts} />
    }
  }

  return (
    <>
      {/* Toast ÏïåÎ¶º ÏãúÏä§ÌÖú */}
      <ToastContainer toasts={toasts} onClose={hideToast} />
      
      <div className="bg-gray-50 dark:bg-gray-900 min-h-screen text-gray-900 dark:text-white">
        <div className="flex">
        {/* Î©îÏù∏ Ïª®ÌÖêÏ∏† ÏòÅÏó≠ */}
        <div className="flex-1 p-8">
          <div className="max-w-7xl mx-auto">
            <div className="flex gap-8">
              {/* Î©îÏù∏ ÏΩòÌÖêÏ∏† ÏòÅÏó≠ */}
              <div className="flex-1">
                {/* Header */}
                <div className="flex items-center justify-between mb-8">
                  <h1 className="text-4xl font-bold text-gray-900 dark:text-white">
                    Í¥ÄÎ¶¨Ïûê ÎåÄÏãúÎ≥¥Îìú
                  </h1>
                  <div className="flex items-center gap-4">
                    {/* ÏïåÎ¶º ÎìúÎ°≠Îã§Ïö¥ - ÏùΩÏßÄ ÏïäÏùÄ Î©îÏãúÏßÄ ÌëúÏãú */}
                    <NotificationDropdown
                      notifications={notifications}
                      showNotifications={showNotifications}
                      onToggle={() => setShowNotifications(!showNotifications)}
                      onNotificationClick={() => {
                        setActiveTab('messages')
                        setShowNotifications(false)
                      }}
                    />
                    
                    {/* Î°úÍ∑∏ÏïÑÏõÉ Î≤ÑÌäº */}
                    <button
                      onClick={logout}
                      className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors flex items-center gap-2"
                    >
                      <FaSignOutAlt />
                      Î°úÍ∑∏ÏïÑÏõÉ
                    </button>
                  </div>
                </div>

                {/* ÌÉ≠ ÏΩòÌÖêÏ∏† */}
                {renderActiveTabContent()}
              </div>

              {/* Î¶¨Ìå©ÌÜ†ÎßÅÎêú RightSidebar Ïª¥Ìè¨ÎÑåÌä∏ ÏÇ¨Ïö© */}
              <RightSidebar
                activeTab={activeTab}
                setActiveTab={setActiveTab}
                activeSettingsSection={activeSettingsSection}
                setActiveSettingsSection={setActiveSettingsSection}
                counts={counts}
                refreshCounts={refreshCounts}
                bookFilters={bookFilters}
                experienceFilters={experienceFilters}
                projectFilters={projectFilters}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    </>
  )
}

export default AdminDashboard
