/**
 * Ïπ¥ÌÖåÍ≥†Î¶¨ Í¥ÄÎ†® Ïú†Ìã∏Î¶¨Ìã∞ Ìï®ÏàòÎì§
 */

/**
 * Ïπ¥ÌÖåÍ≥†Î¶¨ Î¨∏ÏûêÏó¥Ïùò Ï≤´ Í∏ÄÏûêÎ•º ÎåÄÎ¨∏ÏûêÎ°ú Î≥ÄÌôòÌïòÏó¨ ÌëúÏãúÎ™Ö ÏÉùÏÑ±
 * @param category - Ïπ¥ÌÖåÍ≥†Î¶¨ Î¨∏ÏûêÏó¥
 * @returns ÌëúÏãúÏö© Ïπ¥ÌÖåÍ≥†Î¶¨ Ïù¥Î¶Ñ
 */
export const formatCategoryDisplayName = (category: string): string => {
  if (!category) return 'Í∏∞ÌÉÄ'
  
  // ÌäπÎ≥ÑÌïú Í≤ΩÏö∞ Ï≤òÎ¶¨
  const specialCases: { [key: string]: string } = {
    'plc': 'PLC',
    'iot': 'IoT',
    'Îç∞Ïù¥ÌÑ∞Î∂ÑÏÑù': 'Îç∞Ïù¥ÌÑ∞ Î∂ÑÏÑù',
    'Î°úÎ¥áÍ≥µÌïô': 'Î°úÎ¥á Í≥µÌïô',
    'Í∏∞ÌÉÄ': 'Í∏∞ÌÉÄ',
    'all': 'Ï†ÑÏ≤¥'
  }
  
  const lowerCategory = category.toLowerCase()
  if (specialCases[lowerCategory]) {
    return specialCases[lowerCategory]
  }
  
  // ÏùºÎ∞òÏ†ÅÏù∏ Í≤ΩÏö∞: Ï≤´ Í∏ÄÏûêÎßå ÎåÄÎ¨∏ÏûêÎ°ú Î≥ÄÌôò
  return category.charAt(0).toUpperCase() + category.slice(1)
}

/**
 * Ï±Ö Î™©Î°ùÏóêÏÑú Î™®Îì† Í≥†Ïú† Ïπ¥ÌÖåÍ≥†Î¶¨Î•º Ï∂îÏ∂ú
 * @param books - Ï±Ö Î™©Î°ù
 * @returns Í≥†Ïú† Ïπ¥ÌÖåÍ≥†Î¶¨ Î∞∞Ïó¥ (ÏÜåÎ¨∏ÏûêÎ°ú Ï†ïÍ∑úÌôî)
 */
export const extractUniqueCategories = (books: any[]): string[] => {
  const categories = new Set<string>()
  
  // Í∏∞Î≥∏ Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä
  categories.add('all')
  
  // Ï±ÖÏóêÏÑú Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÏ∂ú
  books.forEach(book => {
    if (book.category) {
      categories.add(book.category.toLowerCase())
    }
  })
  
  return Array.from(categories).sort()
}

/**
 * Ïπ¥ÌÖåÍ≥†Î¶¨Î≥Ñ ÌëúÏãúÎ™Ö Îß§Ìïë Í∞ùÏ≤¥ ÏÉùÏÑ±
 * @param categories - Ïπ¥ÌÖåÍ≥†Î¶¨ Î∞∞Ïó¥
 * @returns Ïπ¥ÌÖåÍ≥†Î¶¨ ÌëúÏãúÎ™Ö Îß§Ìïë Í∞ùÏ≤¥
 */
export const createCategoryDisplayNames = (categories: string[]): { [key: string]: string } => {
  const displayNames: { [key: string]: string } = {}
  
  categories.forEach(category => {
    displayNames[category] = formatCategoryDisplayName(category)
  })
  
  return displayNames
}
