import React, { useState } from 'react'
import { FaPlus, FaEdit, FaTrash, FaFolder, FaBars } from 'react-icons/fa'
import { Category, CategoryFormData } from '../../types'
import { useCategoryManagerData, useCategoryUsage } from '../../hooks/useCategories'
import { useCRUDManager } from '../../hooks/useCRUDManager'
import { useToastHelpers } from '../../hooks/useToast'
import CategoryForm from '../forms/CategoryForm'
import { DndContext, closestCenter, DragEndEvent } from '@dnd-kit/core'
import { SortableContext, verticalListSortingStrategy, useSortable, arrayMove } from '@dnd-kit/sortable'
import { CSS } from '@dnd-kit/utilities'

// ÎìúÎûòÍ∑∏ Í∞ÄÎä•Ìïú Ïπ¥ÌÖåÍ≥†Î¶¨ Ïπ¥Îìú Ïª¥Ìè¨ÎÑåÌä∏
interface SortableCategoryCardProps {
  category: Category
  onEdit: () => void
  onDelete: () => void
  isDeleting: boolean
}

const SortableCategoryCard: React.FC<SortableCategoryCardProps> = ({ 
  category, 
  onEdit, 
  onDelete, 
  isDeleting 
}) => {
  const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({ 
    id: category._id 
  })

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
  }

  // Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÇ¨Ïö© ÌÜµÍ≥Ñ Ï°∞Ìöå
  const { data: usageData } = useCategoryUsage(category._id)

  return (
    <div
      ref={setNodeRef}
      style={style}
      className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4 hover:shadow-lg transition-shadow"
    >
      <div className="flex items-start justify-between">
        <div className="flex items-start gap-3 flex-1">
          {/* ÎìúÎûòÍ∑∏ Ìï∏Îì§ */}
          <button
            {...attributes}
            {...listeners}
            className="mt-1 p-2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300 cursor-grab active:cursor-grabbing"
            title="ÎìúÎûòÍ∑∏ÌïòÏó¨ ÏàúÏÑú Î≥ÄÍ≤Ω"
          >
            <FaBars />
          </button>

          <div className="flex-1">
            <h3 className="text-lg font-bold text-gray-900 dark:text-white mb-2">
              {category.name}
            </h3>
            
            {/* ÏÇ¨Ïö© ÌÜµÍ≥Ñ */}
            {usageData && (
              <div className="flex flex-wrap gap-2 text-sm">
                {usageData.usage.books > 0 && (
                  <span className="px-2 py-1 bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200 rounded">
                    üìö ÏÑúÏ†Å {usageData.usage.books}Í∞ú
                  </span>
                )}
                {usageData.usage.projects > 0 && (
                  <span className="px-2 py-1 bg-green-100 dark:bg-green-900 text-green-800 dark:text-green-200 rounded">
                    üöÄ ÌîÑÎ°úÏ†ùÌä∏ {usageData.usage.projects}Í∞ú
                  </span>
                )}
                {usageData.usage.videoLearnings > 0 && (
                  <span className="px-2 py-1 bg-purple-100 dark:bg-purple-900 text-purple-800 dark:text-purple-200 rounded">
                    üìπ ÏòÅÏÉÅ {usageData.usage.videoLearnings}Í∞ú
                  </span>
                )}
                {usageData.usage.videoPlaylists > 0 && (
                  <span className="px-2 py-1 bg-red-100 dark:bg-red-900 text-red-800 dark:text-red-200 rounded">
                    üì∫ Ïû¨ÏÉù Î™©Î°ù {usageData.usage.videoPlaylists}Í∞ú
                  </span>
                )}
                {usageData.usage.total === 0 && (
                  <span className="text-gray-500 dark:text-gray-400 text-xs">
                    ÏÇ¨Ïö© Ï§ëÏù¥ÏßÄ ÏïäÏùå
                  </span>
                )}
              </div>
            )}
          </div>
        </div>

        <div className="flex gap-2 ml-4">
          <button
            onClick={onEdit}
            className="p-2 text-yellow-600 hover:bg-yellow-100 dark:hover:bg-yellow-900 rounded-lg transition-colors"
            title="ÏàòÏ†ï"
          >
            <FaEdit />
          </button>
          <button
            onClick={onDelete}
            disabled={isDeleting}
            className="p-2 text-red-600 hover:bg-red-100 dark:hover:bg-red-900 rounded-lg transition-colors disabled:opacity-50"
            title="ÏÇ≠Ï†ú"
          >
            <FaTrash />
          </button>
        </div>
      </div>
    </div>
  )
}

const CategoryManager: React.FC = () => {
  const { success, error } = useToastHelpers()
  const { 
    categories, 
    isLoading, 
    createCategory, 
    updateCategory, 
    deleteCategory 
  } = useCategoryManagerData()

  const [categoryState, categoryActions] = useCRUDManager<Category>({
    onSave: async (data: CategoryFormData, editingItem) => {
      if (editingItem) {
        const result = await updateCategory(editingItem._id, data)
        if (result.success) {
          success('Ïπ¥ÌÖåÍ≥†Î¶¨ ÏàòÏ†ï ÏôÑÎ£å', 'Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.')
        } else {
          throw result.error
        }
      } else {
        const result = await createCategory(data)
        if (result.success) {
          success('Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä ÏôÑÎ£å', 'ÏÉàÎ°úÏö¥ Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§.')
        } else {
          throw result.error
        }
      }
    },
    onDelete: async (category: Category) => {
      const result = await deleteCategory(category._id)
      if (result.success) {
        success('Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÇ≠Ï†ú ÏôÑÎ£å', `${category.name}Ïù¥(Í∞Ä) ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.`)
      } else {
        // ÏÇ¨Ïö© Ï§ëÏù∏ Í≤ΩÏö∞ ÏóêÎü¨ Î©îÏãúÏßÄ ÌëúÏãú
        const errorMsg = result.error?.response?.data?.message || 'Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§'
        const usage = result.error?.response?.data?.usage
        
        if (usage) {
          const usageDetails = []
          if (usage.books > 0) usageDetails.push(`ÏÑúÏ†Å ${usage.books}Í∞ú`)
          if (usage.projects > 0) usageDetails.push(`ÌîÑÎ°úÏ†ùÌä∏ ${usage.projects}Í∞ú`)
          if (usage.videoLearnings > 0) usageDetails.push(`ÏòÅÏÉÅ ${usage.videoLearnings}Í∞ú`)
          if (usage.videoPlaylists > 0) usageDetails.push(`Ïû¨ÏÉù Î™©Î°ù ${usage.videoPlaylists}Í∞ú`)
          
          error('ÏÇ≠Ï†ú Î∂àÍ∞Ä', `Ïù¥ Ïπ¥ÌÖåÍ≥†Î¶¨Îäî ${usageDetails.join(', ')}ÏóêÏÑú ÏÇ¨Ïö© Ï§ëÏûÖÎãàÎã§. Î®ºÏ†Ä Ìï¥Îãπ Ìï≠Î™©Îì§Ïùò Ïπ¥ÌÖåÍ≥†Î¶¨Î•º Î≥ÄÍ≤ΩÌï¥Ï£ºÏÑ∏Ïöî.`)
        } else {
          error('ÏÇ≠Ï†ú Ïã§Ìå®', errorMsg)
        }
        throw result.error
      }
    },
    onError: (err) => {
      error('ÏûëÏóÖ Ïã§Ìå®', err.message || 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.')
    }
  })

  // ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ÏúºÎ°ú ÏàúÏÑú Î≥ÄÍ≤Ω
  const handleDragEnd = async (event: DragEndEvent) => {
    const { active, over } = event

    if (!over || active.id === over.id) return

    const oldIndex = categories.findIndex(cat => cat._id === active.id)
    const newIndex = categories.findIndex(cat => cat._id === over.id)

    const reorderedCategories = arrayMove(categories, oldIndex, newIndex)

    // ÏàúÏÑú ÏóÖÎç∞Ïù¥Ìä∏
    for (let i = 0; i < reorderedCategories.length; i++) {
      const category = reorderedCategories[i]
      if (category && category.order !== i) {
        await updateCategory(category._id, { name: category.name, order: i })
      }
    }
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="w-12 h-12 border-4 border-primary-600 border-t-transparent rounded-full animate-spin"></div>
      </div>
    )
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-white flex items-center gap-2">
          <FaFolder className="text-yellow-600" />
          Ïπ¥ÌÖåÍ≥†Î¶¨ Í¥ÄÎ¶¨
        </h2>
        <button
          onClick={categoryActions.handleCreate}
          className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors shadow-md"
        >
          <FaPlus />
          Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä
        </button>
      </div>

      {/* Ïπ¥ÌÖåÍ≥†Î¶¨ Ìèº Î™®Îã¨ */}
      {categoryState.showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white dark:bg-gray-800 rounded-lg max-w-md w-full mx-4">
            <div className="p-6">
              <h2 className="text-xl font-semibold mb-4 text-gray-900 dark:text-white">
                {categoryState.editingItem ? 'Ïπ¥ÌÖåÍ≥†Î¶¨ ÏàòÏ†ï' : 'Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä'}
              </h2>
              {categoryState.editingItem ? (
                <CategoryForm
                  data={categoryState.editingItem}
                  onSave={categoryActions.handleSave}
                  onCancel={categoryActions.handleCancel}
                  isSaving={categoryState.isSaving}
                />
              ) : (
                <CategoryForm
                  onSave={categoryActions.handleSave}
                  onCancel={categoryActions.handleCancel}
                  isSaving={categoryState.isSaving}
                />
              )}
            </div>
          </div>
        </div>
      )}

      {/* Ïπ¥ÌÖåÍ≥†Î¶¨ Î™©Î°ù - ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ */}
      <DndContext collisionDetection={closestCenter} onDragEnd={handleDragEnd}>
        <SortableContext items={categories.map(c => c._id)} strategy={verticalListSortingStrategy}>
          <div className="grid gap-4">
            {categories.map((category) => (
              <SortableCategoryCard
                key={category._id}
                category={category}
                onEdit={() => categoryActions.handleEdit(category)}
                onDelete={() => categoryActions.handleDelete(category)}
                isDeleting={categoryState.isDeleting}
              />
            ))}
          </div>
        </SortableContext>
      </DndContext>

      {categories.length === 0 && (
        <div className="text-center py-12 text-gray-500 dark:text-gray-400">
          <FaFolder className="text-6xl mx-auto mb-4 opacity-30" />
          <p>ÏïÑÏßÅ Îì±Î°ùÎêú Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏóÜÏäµÎãàÎã§</p>
          <p className="text-sm mt-2">ÏúÑÏùò "Ïπ¥ÌÖåÍ≥†Î¶¨ Ï∂îÍ∞Ä" Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌïòÏó¨ Ï≤´ Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÎßåÎì§Ïñ¥Î≥¥ÏÑ∏Ïöî</p>
        </div>
      )}
    </div>
  )
}

export default CategoryManager
